import { Link, useLocation } from 'react-router-dom';

import FullLogo from '../imgs/full_logo.svg';

function Navbar() {
    const location = useLocation();
    return (
        <div style={{backgroundColor: '#0e0e0e'}}>
        <div style={{transform: 'translateX(75%)', width: '40%'}}>
            <Link to="/"><img src={FullLogo}/></Link>
            <ul className="list-unstyled d-flex justify-content-around font-weight-bold p-0" dir="rtl">
                <Link to="/" className={`badge ${location.pathname == '/' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>בית</Link>
                <Link to="/services" className={`badge ${location.pathname == '/services' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>שירותים</Link>
                <Link to="/about" className={`badge ${location.pathname == '/about' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>אודות</Link>
                <Link to="/info" className={`badge ${location.pathname == '/info' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>מידע נוסף</Link>
                <Link to="/contact" className={`badge ${location.pathname == '/contact' ? 'text-muted' : ''}`} style={{fontSize: '15px'}}>יצירת קשר</Link>
            </ul>
        </div>
        <br/>
        </div>
    )
}

export default Navbar;